import Vue from "vue";
import Vuex from "vuex";

var tes = JSON.parse(localStorage.getItem("auth-data"));
var data;

if (tes) {
  data = tes;
}

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    api: {
      url: "http://45.127.133.75:3000/api",
      fs: {
        postFS: "/fasilitas",
        getFS: "/fasilitas/details/",
        putFS: "/fasilitas/",
        deleteFS: "/fasilitas/",
        getFSUnit: "/fasilitas/overview/",
        getType: "/fasilitas/list-type",
        postType: "/fasilitas/list-type",
        putType: "/fasilitas/list-type/",
        deleteType: "/fasilitas/list-type/",
      },
      gp: {
        postRating: "/gp/post-rating",
        putRating: "/gp/put-rating/",
        getRating: "/gp/get-rating/",
        getAllRating: "/gp/get-all-rating/",
        getLastRating: "/gp/get-last-rating/",
        getDashboard: "/gp/dash/",
      }
    },
    data: {
      user: null,
      gp: null,
      fasilitas: {
        jenis: null,
        ubah: null,
        ubahJenis: null,
      },
    },
    debug: true,
  },
  mutations: {
    setUserData(state, payload) {
      state.data.user = payload;
    },
    setGPData(state, payload) {
      state.data.gp = payload;
    },
    setJenisFasilitas(state, payload) {
      state.data.fasilitas.jenis = payload;
    },
    setUbahFasilitas(state, payload) {
      state.data.fasilitas.ubah = payload;
    },
    setUbahJenisFasilitas(state, payload) {
      state.data.fasilitas.ubahJenis = payload;
    },
  },
  getters: {
    isDebugging: state => {
      return state.debug;
    },
    getUbahJenisFasilitas: state => {
      return state.data.fasilitas.ubahJenis;
    },
    getUbahFasilitas: state => {
      return state.data.fasilitas.ubah;
    },
    getJenisFasilitas: state => {
      return state.data.fasilitas.jenis;
    },
    getGPData: state => {
      return state.data.gp;
    },
    isAdmin: state => {
      return state.data.user.isAdmin;
    },
    getUserData: state => {
      return state.data.user;
    },
    getUKId: state => {
      return state.data.user.idUK;
    },
    getAPI: state => {
      return state.api;
    }
  }
});
