const beforeEnter = (to, from, next) => {
  var token = JSON.parse(localStorage.getItem("auth-data"));
  !token ? next({ name: "Login" }) : next();
};

let ISPUMenu = {
  path: "/ispu",
  component: DashboardLayout,
  redirect: "/ispu/dasboard",
  name: "ISPU",
  beforeEnter,
  children: [
    {
      path: "dashboard",
      name: "Dashboard ISPU",
      component: ISPUDashboard
    }
  ]
};

